import React, { Fragment, useState } from "react";
import TalkToUsPopup from "./talk-to-us-popup";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import Loading from "./loading";
import talk_to_us from "../../images/talk_to_us.png";
import { Row, Col, Container } from "react-bootstrap";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  // country: Yup.string().label("Country").required(),
  mobile: Yup.string().label("Contact").required(),
  content: Yup.string().label("Content").required(),
  service: Yup.string().label("Service").required(),
});

const TalkToUsNew = (props) => {
  const { db, label = "TALK TO US", isFor } = props;

  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const [number, setNumber] = useState("");

  // const countries = Country.getAllCountries();

  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  // useEffect(() => {
  //   handleToggleTalkToUs(); //children function of interest
  // }, [props.togglePopup]);

  const handleToggleTalkToUs = () => {
    // console.log("handleToggleTalkToUs");
    setTalkToUsShow(!talkToUsShow);
    reset();
  };
  const handleOnChange = (value) => {
    setValue("mobile", value, { shouldValidate: true });
    setNumber(value);
  };
  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile, service } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      service,
      date: new Date(),
    };

    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      setLoading(false);
    }
  };

  const getButtonFor = (isFor) => {
    switch (isFor) {
      case "banner":
        return (
          <span className="primary_btn" onClick={handleToggleTalkToUs}>
            {label}
          </span>
        );
      case "about":
        return (
          <span className="about_rhs_btn" onClick={handleToggleTalkToUs}>
            {label}
          </span>
        );
      case "cta":
        return (
          <span className="cta_btn_div" onClick={handleToggleTalkToUs}>
            {label}
          </span>
        );
      default:
        return (
          <span className="primary_btn" onClick={handleToggleTalkToUs}>
            {label}
          </span>
        );
    }
  };

  return (
    <Fragment>
      {getButtonFor(isFor)}
      {/* {isFor === "banner" ? (
        <button  className="primary_btn" type="button"  onClick={handleToggleTalkToUs}>
          {label}
        </button>
      ) : (
        <button className="header_custom_btn" onClick={handleToggleTalkToUs}>
          {label}
        </button>
      )} */}
      <TalkToUsPopup
        show={talkToUsShow}
        onHide={handleToggleTalkToUs}
        closeButton={true}
        size={"lg"}
        backdrop="static"
        keyboard={false}
      >
         <div className="talk_us_wrapper">
          <Row>
            <Col lg={7}>
              <div className="talk_us_left">
                <form
                  className="form_field"
                  onSubmit={handleSubmit(handleOnSubmit)}
                >
                  <div>
                    <input
                      type="text"
                      placeholder="Enter Your Name"
                      className="talk_input"
                      {...register("name")}
                    />
                    <p className="text-danger mb-0">{errors.name?.message}</p>
                  </div>
                  <div>
                    <input
                      type="mail"
                      placeholder="Enter Your Email"
                      className="talk_input"
                      {...register("email")}
                    />
                    <p className="text-danger mb-0">{errors.email?.message}</p>
                  </div>
                  <div>
                    <PhoneInput
                     className="talk_phone_input"
                     country={"us"}
                     value={number}
                     enableSearch
                     searchPlaceholder=""
                     {...register("mobile")}
                     onChange={handleOnChange}
                      // {...register("mobile")}
                    />
                    <p className="text-danger mb-0">{errors.mobile?.message}</p>
                  </div>

                  <div className="login__field">
                    <select className="talk_input round" {...register("service")}>
                      <option value="">Select your services</option>
                      <option value="crypto exchange development">
                        Crypto Exchange Development
                      </option>
                      <option value="nft marketplace development">
                        NFT Marketplace Development
                      </option>
                      <option value="defi development">DeFi Development</option>
                      <option value="metaverse development">
                        Metaverse Development
                      </option>
                      <option value="blockchain solutions">
                        Blockchain Solutions
                      </option>
                      <option value="web 3.0 development">
                        Web 3.0 Development
                      </option>
                      <option value="crypto wallet development">
                        Crypto Wallet Development
                      </option>
                      <option value="custom software development">
                        Custom Software Development
                      </option>
                      <option value="mobile app development services">
                        Mobile App Development Services
                      </option>
                      <option value="artificial intelligence solutions">
                        Artificial Intelligence Solutions
                      </option>
                      <option value="others"> Others</option>
                    </select>
                    <span className="error_msg_text">
                      <p className="text-danger mb-0">
                        {errors.service?.message}
                      </p>
                    </span>
                  </div>
                  <div>
                    <textarea
                      placeholder="Type Content Here..."
                      rows={5}
                      {...register("content")}
                    ></textarea>
                    <p className="text-danger mb-0">
                      {errors.content?.message}
                    </p>
                  </div>
                  <button
                    type="submit"
                    disabled={loading}
                    className="button login__submit login_new"
                  >
                    <span className="button__text">
                      {" "}
                      Submit {loading && <Loading />}
                    </span>
                    <i className="button__icon fas fa-chevron-right"></i>
                  </button>
                </form>
              </div>
            </Col>
            <Col lg={5}>
              <div className="talk_us_right">
                <h2>Contact Us</h2>
                <p>
                  Feel free to reach out to Justtry Technologies for any
                  inquiries or support. Our team is here to assist you!
                </p>
              </div>
            </Col>
          </Row>
          <div className="talk_to_us_image d-lg-block d-none">
            <img src={talk_to_us} alt="talktousimage" />
          </div>
        </div>
        {/* <div className="screen__content">
          <form onSubmit={handleSubmit(handleOnSubmit)} className="login">
            <div className="login__field">
              <i className="login__icon fas fa-user"></i>
              <input
                type="text"
                className="login__input"
                {...register("name")}
                placeholder="Your Name"
              />
              <p className="text-danger mb-0">{errors.name?.message}</p>
            </div>

            <div className="login__field">
              <input
                type="email"
                className="login__input"
                {...register("email")}
                placeholder="Email"
              />
              <p className="text-danger mb-0">{errors.email?.message}</p>
            </div>
            <div className="login__field">
              <input
                type="text"
                className="login__input"
                placeholder="Skype / Mobile / Whatsapp"
                {...register("mobile")}
              />
              <p className="text-danger mb-0">{errors.mobile?.message}</p>
            </div>
            <div className="login__field">
              <select className="login__input" {...register("service")}>
                <option value="">Select your services</option>
                <option value="crypto exchange development">
                  Crypto Exchange Development
                </option>
                <option value="nft marketplace development">
                  NFT Marketplace Development
                </option>
                <option value="defi development">DeFi Development</option>
                <option value="metaverse development">
                  Metaverse Development
                </option>
                <option value="blockchain solutions">
                  Blockchain Solutions
                </option>
                <option value="web 3.0 development">Web 3.0 Development</option>
                <option value="crypto wallet development">
                  Crypto Wallet Development
                </option>
                <option value="custom software development">
                  Custom Software Development
                </option>
                <option value="mobile app development services">
                  Mobile App Development Services
                </option>
                <option value="artificial intelligence solutions">
                  Artificial Intelligence Solutions
                </option>
                <option value="others"> Others</option>
              </select>
              <p className="text-danger mb-0">{errors.service?.message}</p>
            </div>
            <div className="login__field">
              <select className="login__input" {...register("country")}>
                <option value="">Select Country</option>
                {countries.map((ele, ind) => {
                  return (
                    <option key={ind} value={ele.isoCode}>
                      {ele.name}
                    </option>
                  );
                })}
              </select>
              <p className="text-danger mb-0">{errors.country?.message}</p>
            </div>
            <div className="login__field">
              <textarea
                id="content"
                placeholder="Type content here"
                rows={5}
                className="login__input"
                {...register("content")}
                maxLength={500}
              ></textarea>
              <p className="text-danger mb-0">{errors.content?.message}</p>
            </div>
            <button
              type="submit"
              disabled={loading}
              className="button login__submit"
            >
              <span className="button__text">
                {" "}
                Submit {loading && <Loading />}
              </span>
              <i className="button__icon fas fa-chevron-right"></i>
            </button>
          </form>
        </div>
        <div className="screen__background">
          <span className="screen__background__shape screen__background__shape4"></span>
          <span className="screen__background__shape screen__background__shape3"></span>
          <span className="screen__background__shape screen__background__shape2"></span>
          <span className="screen__background__shape screen__background__shape1"></span>
        </div> */}

       
      </TalkToUsPopup>
    </Fragment>
  );
};

export default TalkToUsNew;
